/* Story.css */

.audio-container {
  max-width: 1100px;
  margin: 60px auto;
  padding: 30px 20px 60px;
  font-family: 'Vazirmatn', Tahoma, sans-serif;
  direction: rtl;
  color: #2c3e50;
  background: #fefefe;
  border-radius: 20px;
  box-shadow: 0 12px 40px rgba(44, 62, 80, 0.12);
  user-select: none;
}

.audio-banner {
  position: relative;
  border-radius: 20px;
  overflow: hidden;
  box-shadow: 0 8px 30px rgba(44, 62, 80, 0.15);
  margin-bottom: 50px;
}

.audio-image {
  width: 100%;
  height:310px;
  top: 0;
  left: 0;
  position: relative;
  object-fit: cover;
  /* filter: brightness(0.85); */
  transition: filter 0.4s ease;
}

.audio-banner:hover .audio-image {
  filter: brightness(1);
}

.audio-banner-text {
  position: absolute;
  top: 30%;
  right: 5%;
  color: #58dd8b;
  max-width: 480px;
  text-shadow: 0 2px 10px rgba(0, 0, 0, 0.482);
}

.audio-banner-text h1 {
  font-size: 36px;
  font-weight: 900;
  margin-bottom: 12px;
  letter-spacing: 0.05em;
}

.audio-banner-text p {
  font-size: 18px;
  margin-bottom: 25px;
  font-weight: 500;
}

.audio-btn {
  background: linear-gradient(135deg, #dde24a, #50e390);
  border: none;
  padding: 14px 34px;
  font-size: 18px;
  font-weight: 700;
  color: white;
  border-radius: 40px;
  cursor: pointer;
  box-shadow: 0 6px 20px rgba(80, 230, 190, 0.45);
  transition: background 0.4s ease, transform 0.3s ease;
  user-select: none;
}

.audio-btn:hover {
  background: linear-gradient(135deg, #c0b73a, #3ddfb0);
  transform: scale(1.08);
}

/* ویژگی‌ها */
.audio-features {
  display: flex;
  justify-content: space-between;
  gap: 32px;
  flex-wrap: wrap;
  margin-bottom: 40px;
}

.feature {
  flex: 1 1 280px;
  background: #fff;
  border-radius: 18px;
  padding: 28px 24px;
  box-shadow: 0 10px 30px rgba(74, 226, 137, 0.12);
  transition: box-shadow 0.3s ease;
  cursor: default;
  user-select: none;
  text-align: center;
}

.feature:hover {
  box-shadow: 0 14px 42px rgba(63, 197, 137, 0.25);
}

.feature span {
  font-size: 36px;
  display: inline-block;
  margin-bottom: 18px;
  color: #4a90e2;
  user-select: none;
}

.feature h3 {
  font-size: 22px;
  font-weight: 800;
  color: #2c3e50;
  margin-bottom: 12px;
}

.feature p {
  font-size: 16px;
  line-height: 1.5;
  color: #495867;
}

/* جستجو */
.search-section {
  margin-bottom: 30px;
  text-align: center;
}

.search-input {
  width: 100%;
  max-width: 420px;
  padding: 12px 18px;
  font-size: 17px;
  border-radius: 25px;
  border: 1px solid #d1d9e6;
  outline: none;
  transition: border-color 0.3s ease;
}

.search-input:focus {
  background: linear-gradient(135deg, #9ae1b4, #46a96a);
}

/* لیست کتاب‌ها */
.audio-list {
  display: flex;
  flex-direction: column;
  gap: 22px;
  margin-bottom: 50px;
}

.audio-card {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: #f9fcff;
  border-radius: 14px;
  padding: 18px 24px;
  box-shadow: 0 5px 18px rgba(74, 144, 226, 0.15);
  transition: box-shadow 0.3s ease;
}

.audio-card:hover {
  box-shadow: 0 8px 26px rgba(74, 226, 178, 0.25);
}

.audio-info h3 {
  font-size: 20px;
  margin-bottom: 6px;
  color: #2c3e50;
}

.audio-info p {
  font-size: 14px;
  color: #617a99;
  margin: 2px 0;
}

.audio-controls {
  display: flex;
  gap: 14px;
  align-items: center;
}

.play-btn,
.fav-btn,
.download-btn {
 background: #65ca8a;
  border: none;
  color: white;
  font-size: 16px;
  font-weight: 700;
  padding: 10px 18px;
  border-radius: 22px;
  cursor: pointer;
  transition: background-color 0.3s ease, transform 0.25s ease;
  user-select: none;
}

.play-btn.playing {
  background: #51b276;
}

.play-btn:hover,
.fav-btn:hover,
.download-btn:hover {
    background: #51b276;
  transform: scale(1.05);
}

/* پلیر صوتی */
.audio-player {
  width: 100%;
  margin-top: 12px;
  outline: none;
  border-radius: 12px;
  box-shadow: 0 3px 10px rgba(74, 226, 183, 0.3);
}

/* بخش نظرات */
.reviews-section {
  background: #f5f9ff;
  border-radius: 20px;
  padding: 28px 26px;
  box-shadow: 0 10px 30px rgba(74, 144, 226, 0.1);
  max-width: 900px;
  margin: 0 auto 80px;
}

.reviews-section h2 {
  font-size: 28px;
  font-weight: 900;
  color: #2c3e50;
  margin-bottom: 30px;
  text-align: center;
}

.review {
  border-bottom: 1px solid #d1d9e6;
  padding-bottom: 20px;
  margin-bottom: 20px;
}

.review:last-child {
  border-bottom: none;
  margin-bottom: 0;
  padding-bottom: 0;
}

.review p {
  font-size: 16px;
  line-height: 1.5;
  color: #495867;
  margin: 4px 0;
}

.review span {
  display: block;
  font-weight: 700;
  font-size: 14px;
  color: #4a90e2;
}

/* ریسپانسیو */
@media (max-width: 900px) {
  .audio-features {
    flex-direction: column;
  }

  .feature {
    max-width: 100%;
    margin-bottom: 30px;
  }

  .audio-banner-text {
    position: static;
    max-width: 100%;
    color: #2c3e50;
    text-shadow: none;
    margin-top: -70px;
    background: #f0f6fccc;
    padding: 20px 28px;
    border-radius: 16px;
  }

  .audio-banner:hover .audio-image {
    filter: brightness(0.85);
  }
}

@media (max-width: 500px) {
  .audio-controls {
    flex-direction: column;
    gap: 10px;
  }
}
